<template>
  <div>
    <stars-show></stars-show>

    <!-- 输入框 -->
    <div class="login_body">
      <div class="login_content">
        <h3>通行码</h3>
        <div class="input_area_content">
          <input
            type="text"
            class="input_content"
            placeholder="请输入..."
            v-model="inputCodeValue"
            @keyup.enter="inputPassCode()"
          />
        </div>
        <span class="error_info" v-show="errorInfo">{{ errorInfo }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import StarsUtil from './stars.vue'
import utils from "@/utils/common"
export default {
  name: 'LoginUtil',
  components: {
    "stars-show": StarsUtil
  },
  data () {
    return {
      errorInfo: "",
      // 输入的通行码
      inputCodeValue: ''
    }
  },
  watch: {
    errorInfo: function (val) {
      this.errorInfo = val
    }
  },
  methods: {

    // 检查 passcode
    inputPassCode () {
      const { result, message } = utils.validateCode(this.inputCodeValue)
      result ? this.$router.push('/') : this.errorInfo = message
    }
  }
}
</script>

<style>
.login_body {
  width: 100%;
  height: 100%;
}

.login_content {
  width: 280px;
  height: 100px;
  background: #ffffff3b;
  border-radius: 15px;
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  box-shadow: 5px 5px 40px 1px;
  padding: .4rem 1.6rem 1.5rem 1.6rem;
}

.input_area_content {
  width: 75%;
  background: #ffffff7a;
  height: 24px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  border-radius: 4px;
}

.input_content {
  height: 22px;
  width: 100%;
  padding-left: 2px;
  transform: translate(-2px, -2px);
  background: transparent;
  font-weight: 700;
  padding: .1rem .4rem;
  font-size: 15px;
}

.error_info {
    margin: 0;
    font-size: 14px;
    color: #ff00008c;
    padding: .1rem 0;
}
</style>



